.App {
  background-color: #f4f9fc;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.content-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #0f1b61;
  padding: 3em 1em;
}

.header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #0f1b61;
  padding: 1em;
}

footer {
  background-image:
    linear-gradient(to bottom, rgba(244, 249, 252, 1), rgba(244, 249, 252, 0.9) 30%, rgba(244, 249, 252, 0.4) 40%, rgba(0, 0, 0, 0) 50%),
    url('images/DSC_5081.jpg');
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: #0f1b61;
}

.profile-picture {
  margin: 3rem;
}

.header-text {
  font-family: 'Bitter', serif !important;
  text-align: left;
  position: relative;
  z-index: 2;
}

.header-text h1 {
  font-size: 5rem;
  margin-bottom: 30px;
}

.header-text h4 {
  font-size: 2rem;
  line-height: 1.65;
}

a.social-icon {
  border-radius: 0.5rem;
  background: #cfeaf4;
  color: #0f1b61;
  display: inline-block;
  text-align: center;
  line-height: 3.5rem;
  width: 3.5rem;
  height: 3.5rem;
  margin: 1rem;
}

a.social-icon:hover {
  background: #aadcec;
  color: #7f00e0;
}

.content-container h3 {
  font-family: 'Bitter', serif !important;
  font-size: 3rem;
}

.content-container .title {
  font-family: 'Bitter', serif !important;
  text-align: center;
  margin-bottom: 1em;
}

.content-container h3.title {
  font-size: 3rem;
}

.content-container .text {
  text-align: center;
  margin-top: 3em;
}

.dev-icons {
  text-align: center;
}

.dev-icons i {
  color: #0f1b61;
  display: inline-block;
  text-align: center;
  font-size: 4rem;
  line-height: 4rem;
  width: 5rem;
  height: 5rem;
  margin: 0.75rem;
}

.dev-icons i:hover {
  color: #7f00e0;
}

.content-container ul {
  margin-top: 1.5rem;
  list-style-type: "â€¢";
  padding-left: 1rem;
}

.content-container li {
  text-align: left;
  font-weight: lighter;
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  padding-left: 1rem;
}

.experience-grid {
  padding-bottom: 8rem;
}

.experience-date-location {
  font-size: 1.5rem;
  text-align: left;
  font-weight: lighter;
}

.experience-title {
  font-weight: lighter;
  font-size: 2.5rem;
}

.experience-description {
  text-transform: uppercase;
  font-weight: lighter;
  font-size: 2rem;
}